// Update lesson progress (new function for lesson-level tracking)
export const updateLessonProgress = async (moduleId, lessonId, progressData) => {
  try {
    console.log(' contentManager.updateLessonProgress called with:', { moduleId, lessonId, progressData })
    const endpoint = `/api/learning/modules/${moduleId}/lessons/${lessonId}/progress`
    console.log(' API endpoint:', endpoint)
    
    const response = await apiPost(endpoint, progressData)
    console.log(' API response received:', response)
    console.log(' Response status:', response.status)
    console.log(' Response ok:', response.ok)
    
    if (response.ok) {
      const data = await response.json()
      console.log(' Lesson progress updated successfully:', data)
      return data
    } else {
      console.log(' API response not ok, status:', response.status)
      const errorData = await response.json()
      console.log(' Error response body:', errorData)
      throw new Error(errorData.error || `HTTP error! status: ${response.status}`)
    }
  } catch (error) {
    console.warn('Backend unavailable, storing progress locally:', error)
    // Store locally as fallback
    const localProgress = {
      module_id: moduleId,
      lesson_id: lessonId,
      completed: progressData.completed || false,
      score: progressData.score || 0,
      started_at: new Date().toISOString(),
      completed_at: progressData.completed ? new Date().toISOString() : null
    }
    localStorage.setItem(`lesson_progress_${moduleId}_${lessonId}`, JSON.stringify(localProgress))
    return { progress: localProgress }
  }
}

// Update user progress (kept for backward compatibility)
export const updateProgress = async (moduleId, progressData) => {
  try {
    const response = await apiPost(API_ENDPOINTS.PROGRESS(moduleId), progressData)
    if (response.ok) {
      const data = await response.json()
      // Also store locally as backup
      localStorage.setItem(`module_progress_${moduleId}`, JSON.stringify(data.progress))
      return data
    } else {
      throw new Error('Failed to update progress')
    }
  } catch (error) {
    console.warn('Backend unavailable, storing progress locally:', error)
    // Store locally as fallback
    const localProgress = {
      module_id: moduleId,
      completed: progressData.completed || false,
      score: progressData.score || 0,
      started_at: new Date().toISOString(),
      completed_at: progressData.completed ? new Date().toISOString() : null
    }
    localStorage.setItem(`module_progress_${moduleId}`, JSON.stringify(localProgress))
    return { progress: localProgress }
  }
}

