import { useState, useEffect, useCallback } from 'react';
import { API, API_ENDPOINTS, apiGet, apiPost, apiPut, apiDelete } from '../lib/api';
import { cloudinaryUnsignedUpload } from '../lib/utils';

export const useCommunity = () => {
  const [reports, setReports] = useState([]);
  const [trendingThreats, setTrendingThreats] = useState([]);
  const [communityStats, setCommunityStats] = useState(null);
  const [alerts, setAlerts] = useState([]);
  const [leaderboard, setLeaderboard] = useState([]);
  const [latestReports, setLatestReports] = useState([]);
  const [myStats, setMyStats] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [pagination, setPagination] = useState(null);
  const [hasMore, setHasMore] = useState(true);

  // Fetch community reports
  const fetchReports = useCallback(async (filters = {}, append = false) => {
    try {
      setIsLoading(true);
      setError(null);
      
      const queryParams = new URLSearchParams(filters).toString();
      const endpoint = `${API_ENDPOINTS.COMMUNITY.REPORTS}${queryParams ? `?${queryParams}` : ''}`;
      
      const response = await apiGet(endpoint);
      if (response.ok) {
        const data = await response.json();
        
        if (append && data.reports) {
          setReports(prev => [...prev, ...data.reports]);
        } else {
          setReports(data.reports || data);
        }
        
        if (data.pagination) {
          setPagination(data.pagination);
          setHasMore(data.pagination.has_next);
        }
        
        return data;
      } else {
        throw new Error('Failed to fetch community reports');
      }
    } catch (err) {
      setError(err.message);
      console.error('Error fetching reports:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch trending threats
  const fetchTrendingThreats = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      
      const response = await apiGet(API_ENDPOINTS.COMMUNITY.TRENDING);
      if (response.ok) {
        const data = await response.json();
        // Backend returns { trending_threats: [...] }
        setTrendingThreats(data.trending_threats || data);
        return data;
      } else {
        throw new Error('Failed to fetch trending threats');
      }
    } catch (err) {
      setError(err.message);
      console.error('Error fetching trending threats:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch latest reports: 10 most recent with status APPROVED or VERIFIED and urgency HIGH
  const fetchLatestReports = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      // Fetch a larger page and filter client-side to meet multi-status and urgency criteria
      const params = new URLSearchParams({ per_page: 50, sort: 'newest', include_all: 'true' }).toString();
      const response = await apiGet(`${API_ENDPOINTS.COMMUNITY.REPORTS}?${params}`);
      if (response.ok) {
        const data = await response.json();
        const all = data.reports || [];
        const filtered = all.filter(r => (r.status === 'APPROVED' || r.status === 'VERIFIED') && r.verified === true && r.urgency === 'HIGH');
        const topTen = filtered.slice(0, 10);
        setLatestReports(topTen);
        return { reports: topTen };
      } else {
        throw new Error('Failed to fetch latest reports');
      }
    } catch (err) {
      setError(err.message);
      console.error('Error fetching latest reports:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch a single report with full details (including all comments)
  const fetchReportById = useCallback(async (reportId) => {
    try {
      setIsLoading(true);
      setError(null);
      const response = await apiGet(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}`);
      if (response.ok) {
        const data = await response.json();
        return { success: true, report: data };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to fetch report');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // List comments with pagination
  const fetchComments = useCallback(async (reportId, { page = 1, per_page = 20 } = {}) => {
    try {
      setIsLoading(true);
      setError(null);
      const params = new URLSearchParams({ page, per_page }).toString();
      const response = await apiGet(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}/comments?${params}`);
      if (response.ok) {
        const data = await response.json();
        return { success: true, ...data };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to fetch comments');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Delete a comment
  const deleteComment = useCallback(async (commentId) => {
    try {
      setIsLoading(true);
      setError(null);
      const response = await apiDelete(`/api/community/comments/${commentId}`);
      if (response.ok) {
        return { success: true };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to delete comment');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch community statistics
  const fetchCommunityStats = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      
      const response = await apiGet(API_ENDPOINTS.COMMUNITY.STATS);
      if (response.ok) {
        const data = await response.json();
        setCommunityStats(data);
        return data;
      } else {
        throw new Error('Failed to fetch community statistics');
      }
    } catch (err) {
      setError(err.message);
      console.error('Error fetching community stats:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch community alerts
  const fetchAlerts = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      
      const response = await apiGet(API_ENDPOINTS.COMMUNITY.ALERTS);
      if (response.ok) {
        const data = await response.json();
        // Backend returns { alerts: [...], pagination: { ... } }
        setAlerts(data.alerts || data);
        return data;
      } else {
        throw new Error('Failed to fetch community alerts');
      }
    } catch (err) {
      setError(err.message);
      console.error('Error fetching alerts:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch leaderboard
  const fetchLeaderboard = useCallback(async ({ period = '90d' } = {}) => {
    try {
      setIsLoading(true);
      setError(null);
      const qs = new URLSearchParams({ period }).toString();
      const response = await apiGet(`${API_ENDPOINTS.COMMUNITY.LEADERBOARD}?${qs}`);
      if (response.ok) {
        const data = await response.json();
        setLeaderboard((data && data.leaderboard) ? data.leaderboard : (Array.isArray(data) ? data : []));
        return data;
      } else {
        throw new Error('Failed to fetch leaderboard');
      }
    } catch (err) {
      setError(err.message);
      console.error('Error fetching leaderboard:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch current user's reporting stats
  const fetchMyStats = useCallback(async ({ period = '90d' } = {}) => {
    try {
      setIsLoading(true);
      setError(null);
      const qs = new URLSearchParams({ period }).toString();
      const response = await apiGet(`${API_ENDPOINTS.COMMUNITY.MY_STATS}?${qs}`);
      if (response.ok) {
        const data = await response.json();
        setMyStats(data);
        return data;
      } else {
        throw new Error('Failed to fetch my stats');
      }
    } catch (err) {
      setError(err.message);
      console.error('Error fetching my stats:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Create new report
  const createReport = useCallback(async (reportData) => {
    try {
      setIsLoading(true);
      setError(null);
      
      // Map UI fields to backend expected payload
      const payload = {
        threat_type: reportData.threat_type,
        description: reportData.description,
        location: reportData.location,
        urgency: reportData.urgency || 'MEDIUM'
      };
      const response = await apiPost(API_ENDPOINTS.COMMUNITY.REPORTS, payload);
      if (response.ok) {
        const { report } = await response.json();
        setReports(prev => [report, ...prev]);
        return { success: true, report };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to create report');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Upload media to a report (accepts File or remote URL)
  const uploadReportMedia = useCallback(async (reportId, fileOrUrl) => {
    try {
      setIsLoading(true);
      setError(null);

      let response;
      if (fileOrUrl instanceof File) {
        // Direct-to-Cloudinary to avoid server size limits
        const uploaded = await cloudinaryUnsignedUpload(fileOrUrl, { folder: 'community_reports' });
        response = await apiPost(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}/media`, {
          media_url: uploaded.url,
          media_type: uploaded.resource_type === 'video' ? 'video' : 'image'
        });
      } else {
        response = await apiPost(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}/media`, {
          media_url: String(fileOrUrl),
          media_type: 'image'
        });
      }

      if (response.ok) {
        const data = await response.json();
        const media = data.media || data;
        // Optionally update report media in local state
        setReports(prev => prev.map(r => r.id === reportId ? { ...r, media: [...(r.media || []), media] } : r));
        return { success: true, media };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.error || errorData.message || 'Failed to upload media');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, [setReports]);

  // Delete report (owner or admin)
  const deleteReport = useCallback(async (reportId) => {
    try {
      setIsLoading(true);
      setError(null);
      const response = await apiDelete(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}`);
      if (response.ok) {
        setReports(prev => prev.filter(r => r.id !== reportId));
        return { success: true };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to delete report');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Vote on report
  const voteOnReport = useCallback(async (reportId, voteType) => {
    try {
      setIsLoading(true);
      setError(null);
      
      // voteType should be 'up' | 'down'
      const response = await apiPost(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}/vote`, {
        vote_type: voteType
      });
      if (response.ok) {
        const data = await response.json();
        const updatedReport = data.report || data;
        // Update the report in the list
        setReports(prev => prev.map(r => 
          r.id === reportId ? { ...updatedReport } : r
        ));
        return { success: true, data: updatedReport };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to vote on report');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Verify report
  const verifyReport = useCallback(async (reportId, verificationData) => {
    try {
      setIsLoading(true);
      setError(null);
      
      const response = await apiPost(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}/verify`, verificationData);
      if (response.ok) {
        const data = await response.json();
        const updatedReport = data.report || data;
        // Update the report in the list
        setReports(prev => prev.map(r => 
          r.id === reportId ? { ...updatedReport } : r
        ));
        return { success: true, data: updatedReport };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to verify report');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Add comment to report
  const addComment = useCallback(async (reportId, commentData) => {
    try {
      setIsLoading(true);
      setError(null);
      
      const response = await apiPost(`${API_ENDPOINTS.COMMUNITY.REPORTS}/${reportId}/comment`, commentData);
      if (response.ok) {
        const result = await response.json();
        const newComment = { id: result.comment_id, comment: commentData.comment };
        // Update the report in the list (UI may render simple comments)
        setReports(prev => prev.map(r => 
          r.id === reportId ? { ...r, comments: [...(r.comments || []), newComment] } : r
        ));
        return { success: true, comment: newComment };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to add comment');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Create alert
  const createAlert = useCallback(async (alertData) => {
    try {
      setIsLoading(true);
      setError(null);
      // Map UI fields to backend expected payload
      const payload = {
        message: alertData.message,
        severity: (alertData.priority || 'medium').toUpperCase(),
        threat_type: alertData.title
      };
      const response = await apiPost(API_ENDPOINTS.COMMUNITY.ALERTS, payload);
      if (response.ok) {
        const { alert } = await response.json();
        setAlerts(prev => [alert, ...prev]);
        return { success: true, alert };
      } else {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to create alert');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Load all data
  const loadAllData = useCallback(async ({ period = '90d' } = {}) => {
    await Promise.all([
      fetchReports({}, false), // Reset reports, don't append
      fetchTrendingThreats(),
      fetchLatestReports(),
      fetchCommunityStats(),
      fetchAlerts(),
      fetchLeaderboard({ period }),
      fetchMyStats({ period })
    ]);
  }, [fetchReports, fetchTrendingThreats, fetchLatestReports, fetchCommunityStats, fetchAlerts, fetchLeaderboard, fetchMyStats]);

  // Clear error
  const clearError = useCallback(() => setError(null), []);

  return {
    reports,
    trendingThreats,
    communityStats,
    alerts,
    leaderboard,
    latestReports,
    myStats,
    isLoading,
    error,
    pagination,
    hasMore,
    fetchReports,
    fetchTrendingThreats,
    fetchLatestReports,
    fetchComments,
    deleteComment,
    fetchReportById,
    fetchCommunityStats,
    fetchAlerts,
    fetchLeaderboard,
    fetchMyStats,
    createReport,
    voteOnReport,
    verifyReport,
    addComment,
    createAlert,
    uploadReportMedia,
    deleteReport,
    loadAllData,
    clearError
  };
};
